/* ~~ import font ~~ */
@font-face {
    font-family: 'NeonFont';
    src: url(/static/media/NeonFont.696ade59.otf) format('opentype');
}

/* ~~ font ~~ */
ul {
    font-family: NeonFont;
    font-style: bold;
    font-size: 2.5vw;
    line-height: 1.2;

    /* make ul horizontal and remove bullet points */
    list-style: none;
    display: flex;
}

li {
    margin-right: 50px;
    /* on hover change pointer */
    cursor: pointer;
}

a {
    /* remove link formatting (blue & underlined) */
    text-decoration: none;
    color: inherit;
}

/* hover neon effect */
/* references: https://codepen.io/AllThingsSmitty/pen/VzXrgY https://codepen.io/rhsuman/embed/PagVoP?height=300&theme-id=33801&slug-hash=PagVoP&default-tab=result&user=rhsuman&embed-version=2&pen-title=Animated%20Neon%20text%20hover&preview=true */
.neon {
    color: #f1e273;
    text-shadow:
        0 0 5px #F48C06,
        0 0 10px #F48C06,
        0 0 20px #F48C06,
        0 0 40px #BB3E03,
        0 0 80px #BB3E03,
        0 0 90px #BB3E03,
        0 0 100px #BB3E03,
        0 0 150px #BB3E03;
}

li:hover {
    -webkit-animation: neon .04s ease-in-out infinite alternate;
    animation: neon .04s ease-in-out infinite alternate;
}

/*-- Animation Keyframes for navbar--*/
@-webkit-keyframes neon {
    from {
        text-shadow:
            0 0 6px rgba(255, 255, 255, 0.92),
            0 0 30px rgba(255, 255, 255, 0.34),
            0 0 12px rgba(241, 226, 115, 0.52),
            0 0 21px rgba(241, 226, 115, 0.92),
            0 0 34px rgba(241, 226, 115, 0.78),
            0 0 54px rgba(241, 226, 115, 0.92);
    }

    to {
        text-shadow:
            0 0 6px rgba(255, 255, 255, 0.98),
            0 0 30px rgba(255, 255, 255, 0.42),
            0 0 12px rgba(241, 226, 115, 0.58),
            0 0 22px rgba(241, 226, 115, 0.84),
            0 0 38px rgba(241, 226, 115, 0.88),
            0 0 60px #F48C06;
    }
}

@keyframes neon {
    from {
        text-shadow:
            0 0 6px rgba(255, 255, 255, 0.92),
            0 0 30px rgba(255, 255, 255, 0.34),
            0 0 12px rgba(241, 226, 115, 0.52),
            0 0 21px rgba(241, 226, 115, 0.92),
            0 0 34px rgba(241, 226, 115, 0.78),
            0 0 54px rgba(241, 226, 115, 0.92);
    }

    to {
        text-shadow:
            0 0 6px rgba(255, 255, 255, 0.98),
            0 0 30px rgba(255, 255, 255, 0.42),
            0 0 12px rgba(241, 226, 115, 0.58),
            0 0 22px rgba(241, 226, 115, 0.84),
            0 0 38px rgba(241, 226, 115, 0.88),
            0 0 60px #F48C06;
    }
}


/* optimize for small screen and mobile */
@media (max-width: 800px) {
    ul {
        font-size: 8vw;
        /* make ul horizontal and remove bullet points */
        display: block;
        text-align: right;
        line-height: 1.8;
    }

    .navbar {
        top: 80%;   
        justify-content: right;
        left: 65%;
    }
}

/* ~~ PopUp div ~~ */
.popBox {
    /* whats the diff b/w absolute & fixed? */
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: #FDF4DC;
    width: 80%;
    height: 70%;
    padding: 15px;
    border-radius: 20px;
    /* give it that disco fade */
    box-shadow:
        0 0 2.5px #F48C06,
        0 0 5px #F48C06,
        0 0 10px #F48C06,
        0 0 20px #BB3E03,
        0 0 40px #BB3E03,
        0 0 45px #BB3E03,
        0 0 50px #BB3E03,
        0 0 75px #BB3E03;
    /* bring to front */
    z-index: 9999;
}

 .popBox-close {
    position: absolute;
    top: 50%;
    right: 50%;
    cursor: pointer;
    font-size: 20px;
    font-weight: bold;
    
  } 
  
/* ~~ font ~~ */
  .popFont {
    font-family: 'FooterFont';
    font-size: 2vw;
    /* font-style: bold; */
    text-align: left;
    color: #22110d;
}
